.about-me {
	box-shadow: inset 0px 3px 6px 0px rgba(0, 0, 0, 0.4), inset 0px -3px 6px 0px rgba(0, 0, 0, 0.4);
	@include background ($about_460, no-repeat, cover, center, center);

	&__content {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-evenly;
		align-items: center;
		align-content: center;
		padding-top: 5vh;
		padding-bottom: 5vh
	}

	&__text {
		flex-basis: 100%;
	}
}

.no-webp .about-me {
	@include background ($about_460, no-repeat, cover, center, center);
}

.webp .about-me {
	@include background ($about_460webp, no-repeat, cover, center, center);
}

.card {
	flex-basis: 100%;

	&__picture {
		position: relative;
	}

	&__photo {
		@include box (200px);
		border-radius: 50%;
		border: 5px solid rgba(255, 255, 255, 0.4);
		box-shadow: 0 6px 24px rgba(255, 255, 255, 0.2);
		margin-bottom: 20px;

	}

	&__logo {
		@include box (80px);
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateX(calc(-50% + 105px)) translateY(calc(-50% + 60px));
		border-radius: 50%;
		border: 5px solid $colorDirtyWhite;


	}


	&__title {
		font-size: 1rem;
		letter-spacing: .4rem;
		line-height: 1.5rem;
		text-transform: uppercase;
		padding: 1vh 0 0.5vh 0;
		opacity: .7;
		color: $colorAccent;
	}

	&__subtitle {
		font-size: 2rem;
		text-shadow: $textShadowSub;
		line-height: 2rem;
		margin: 4vh 0;
		font-weight: 700;
		color: $colorDirtyWhite;
	}
}



// *** Media Queries ***
@include mq (medium) {
	.no-webp .about-me {
		@include background ($about_800, no-repeat, cover, center, center);
	}

	.webp .about-me {
		@include background ($about_800webp, no-repeat, cover, center, center);
	}
}

@include mq (large) {
	.about-me {
		&__content {
			height: 100vh;
			padding-top: 0;
			padding-bottom: 0;
		}

		&__text {
			flex-basis: 55%;
		}
	}

	.card {
		flex-basis: 30%;
	}
}

@include mq (xlarge) {
	.no-webp .about-me {
		@include background ($about_1366, no-repeat, cover, center, center);

	}

	.webp .about-me {
		@include background ($about_1366webp, no-repeat, cover, center, center);
	}
}

@include mq (xxlarge) {
	.no-webp .about-me {
		@include background ($about_1920, no-repeat, cover, center, center);
	}

	.webp .about-me {
		@include background ($about_1920webp, no-repeat, cover, center, center);
	}

	.card {
		&__title {
			font-size: 1.3rem;
			line-height: 1.8rem;
		}
	}
}