.portfolio {
	box-shadow: inset 0px -4px 7px 0px rgba(0, 0, 0, 0.5);
	background: $bgGradient;

	&__content {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-evenly;
		align-items: center;
		align-content: center;
		padding-top: 5vh;
		padding-bottom: 5vh;
	}

	&__text {
		padding-bottom: 10vh;
	}

	&__title {
		color: $colorDirtyWhite;
		text-shadow: $textShadow;
	}

	&__subtitle {
		color: $colorAccent;
		text-shadow: $textShadow;
	}
}

.bg-smoke {
	//@include background ($smoke, no-repeat, cover, center, top);
}

.project {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-evenly;
	align-items: center;
	align-content: center;

	&__info {
		flex-basis: 100%;
		order: 2;
	}

	&__heading {
		font-size: 1rem;
		letter-spacing: .4rem;
		line-height: 1.5rem;
		text-transform: uppercase;
		padding: 1vh 0 0.5vh 0;
		color: $colorAccent;
	}

	&__img {
		flex-basis: 100%;
		max-width: 300px;
		height: auto;
	}

	&__links {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-evenly;
		align-items: center;
		align-content: center;
		padding-top: 2rem;
	}
}


// *** Media Queries ***

@include mq (large) {
	.portfolio {
		&__content {
			height: 100vh;
			padding-top: 0;
			padding-bottom: 0;
		}
	}


	.project {
		&__info {
			flex-basis: 45%;
			order: 0;

			&--reverse {
				order: 1;
			}
		}

		&__img {
			flex-basis: 50%;
			max-width: 500px;
			height: auto;
		}

		&__links {
			flex-flow: row nowrap;
		}

		&__link {
			flex-basis: 40%;
		}
	}
}

@include mq (xxlarge) {
	.project {
		&__heading {
			font-size: 1.3rem;
			letter-spacing: .4rem;
			line-height: 1.8rem;
		}
	}
}